.navbar {
  position: absolute; 
  top: 0px;
  width: 375px;

  padding: 0;
  padding-top: 20px;
  
  display: flex;
  flex-direction: column;
}

.bottom-drawer {
  position: absolute; 
  bottom: 0px;
  width: 375px;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-content: center;
  background-color: #FFFFFF;

  border-radius: 10px;
  padding-top: 20px;
  padding-left: 20px;
  padding-bottom: 20px;
}

.nav-buttons {
  display: flex;
  justify-content: space-between;
  margin-right: 20px;
  margin-top: 10px;
}

.back-button {
  width: 30%;
  display: flex;
  justify-content: center;
  align-items: center;
  
  background: #FFFFFF;
  border: 1px solid #E4E4E4;
  box-sizing: border-box;
  border-radius: 10px;

  font-family: Futura;
  font-style: normal;
  font-weight: bold;
  font-size: 12px;
  letter-spacing: 0.08em;
  color: #009CDB;

  padding: 17px 0px;
}

.back-image {
  margin-right: 10px;
}

.continue-button {
  width: 67%;
  display: flex;
  justify-content: center;
  align-items: center;

  background: linear-gradient(180deg, rgba(255, 255, 255, 0.3) 0%, rgba(255, 255, 255, 0) 100%), #009CDB;
  border-radius: 5px;
  border: none;

  font-family: Futura;
  font-style: normal;
  font-weight: bold;
  font-size: 12px;
  text-align: center;
  letter-spacing: 0.08em;
  text-transform: uppercase;

  color: #FFFFFF;
}

.continue-price {
  /* border: 1px solid white; */
  border: 1px solid #60C1E9;
  padding: 6px 12px;
  margin-right: 12px;
  border-radius: 5px;
}

