.carousel-provider {
  margin-left: -5px;
}

.slider, .tray, .tray-wrap {
  padding-bottom: 4px;
}

.slide-container {
  display: flex; 
  flex-direction: column; 
  align-items: center; 
  
  border: 1px solid #E4E4E4;
  border-radius: 10px;

  margin-left: 5px;
  margin-right: 5px;
  
  padding-bottom: 10px;  
}

#active-product-0 {
  display: flex; 
  flex-direction: column; 
  align-items: center; 
  
  border: 2px solid #009CDB;
  border-radius: 10px;

  margin-left: 5px;
  margin-right: 5px;
}

#add-0 {
  display: flex;
  justify-content: center;
  align-content: center;
  /* position: 'absolute'; */
  position: fixed;
  bottom: -5px;

  display: block; 
}

.image {
  height: 65px;
  width: auto;
  margin: 10px 0px;
}

.p-image {
  max-height: 100%;
  width: auto;
}

.name {
  margin-top: 5px;
  height: 27px;
  padding: 0px 15px;

  font-family: Futura;
  font-style: normal;
  font-weight: bold;
  line-height: 120%;
  font-size: 12px;
  text-align: center;
  letter-spacing: 0.08em;
  color: #333333;
  overflow: hidden;
}

.price {
  margin-top: 7px;
  margin-bottom: 17px;
  font-family: Futura;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  text-align: center;
  letter-spacing: 0.04em;
  color: #333333;
}

.add {
  display: flex;
  justify-content: center;
  align-content: center;
  /* position: 'absolute'; */
  position: fixed;
  bottom: -5px;

  display: none;
} 


.button {
  padding: 8px 30px;
  
  background: linear-gradient(180deg, rgba(255, 255, 255, 0.3) 0%, rgba(255, 255, 255, 0) 100%), #009CDB;
  border: none;
  border-radius: 5px; 
  font-family: Futura;
  font-style: normal;
  font-weight: bold;
  font-size: 12px;
  text-align: center;
  letter-spacing: 0.08em;
  color: #FFFFFF;

  box-shadow: 4px 4px 20px rgba(0, 0, 0, 0.13);
} 
