.ci-container {
  padding-left: 15px;
  border-top: 1px solid #E4E4E4;

  display: flex;
} 

.ci-image-container {
  padding: 7px;
  margin-top: 15px;
  margin-right: 12px;

  width: 72px;
  height: 72px;

  display: flex;
  justify-content: center;
  align-items: center;

  background: #FFFFFF;
  border: 1.26207px solid #E4E4E4;
  box-sizing: border-box;
  border-radius: 6.31035px;
}

.ci-image {
  width: 60px;
  height: 60px;
}

.ci-details {
  display: flex;
  flex-direction: column;

  margin-top: 18px;
  margin-bottom: 16px;
}

.ci-details-text {
  display: flex;
  flex-direction: column; 

  width: 185px;
}

.ci-details-category {
  font-family: Futura;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 120%;
  letter-spacing: 0.04em;

  color: #999999;
}

.ci-details-product {
  height: 35px;
  margin-top: 2px;

  font-family: Futura;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 130%;
  color: #222222; 
}

.ci-details-price {
  margin-top: 10px;

  font-family: Futura;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 120%;
  color: #222222;
}

.ci-details-buttons {
  margin-top: 16px;
}

.ci-details-sfl {
  height: 44px;
  width: 130px;
  margin-right: 12px;

  background: #FFFFFF;
  border: 1px solid #E4E4E4;
  box-sizing: border-box;
  border-radius: 5px;

  font-family: Futura;
  font-style: normal;
  font-weight: bold;
  font-size: 10px;
  line-height: 120%;
  /* identical to box height, or 12px */

  letter-spacing: 0.08em;
  text-transform: uppercase;

  /* Primary / Blue */

color: #009CDB;
}

.ci-details-trash {
  height: 44px;
  width: 44px;

  background: #FFFFFF;
  border: 0.857143px solid #E4E4E4;
  box-sizing: border-box;
  border-radius: 42.8571px;
}

.ci-details-button {
  display:flex;
}

.ci-change-count {
  margin-left: auto;
  margin-top: 16px;

  display: flex;
  flex-direction: column;
}

.ci-change-minus {
  display: flex;
  justify-content: center;
  align-items: center;

  height: 32px;
  width: 32px;

  background: #FFFFFF;
  border: none;
  box-sizing: border-box;
  border-radius: 80px;
}

.ci-change-number {
  display: flex;
  justify-content: center;
  align-items: center;

  height: 32px;
  width: 32px;

  background: #FFFFFF;
  border: 0.8px solid #E4E4E4;
  box-sizing: border-box;
  border-radius: 80px;
  

}

.ci-change-plus {
  display: flex;
  justify-content: center;
  align-items: center;

  height: 32px;
  width: 32px;

  background: #FFFFFF;
  border: none;
  box-sizing: border-box;
  border-radius: 80px;
}