.scarousel-container {
  display: flex;
  font-family: Futura;
  font-style: normal;
  font-weight: bold;
  font-size: 10px;
  letter-spacing: 0.08em;
  text-transform: uppercase;
  color: #222222;
  text-align: center;
}

.anchor {
  margin-right: 10px;
}

.anchor-sslide {
  height: 40px;
  
  background: linear-gradient(180deg, rgba(255, 255, 255, 0.3) 0%, rgba(255, 255, 255, 0) 100%), #009CDB;
  border: none;
  border-radius: 5px; 

  padding: 5px 10px;
}

.anchor-sslide {
  width: 100%
}

.sslide {
  height: 40px;
  overflow: hidden;
  background-color: rgb(255, 255, 255);

  border: 1px solid #E4E4E4;
  border-radius: 5px; 

  margin-bottom: 10px;
  margin-right: 10px; 
  padding-top: 5px;
  padding-bottom: 5px;
  padding-left: 10px;
  padding-right: 10px;

}

#sslide-0 {
  height: 40px;
  overflow: hidden;
  background: linear-gradient(180deg, rgba(255, 255, 255, 0.3) 0%, rgba(255, 255, 255, 0) 100%), #009CDB;

  border: 1px solid #E4E4E4;
  border-radius: 5px; 

  margin-right: 10px; 
  padding-top: 5px;
  padding-bottom: 5px;
  padding-left: 10px;
  padding-right: 10px;

}
