.nav-container {
  height: 100%;
  width: 355px;
}

.nav {
  display: flex; 
  flex-direction: column; 
  justify-content: center;
  background: linear-gradient(180deg, rgba(255, 255, 255, 0.2) 0%, rgba(255, 255, 255, 0) 100%), #BB4B82;
  border-radius: 5px;
}

.heading-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 15px;
}

.title {
  font-family: Futura;
  font-style: normal;
  font-weight: bold;
  font-size: 12px;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: #FFFFFF;
  padding-top: 3px;
}

