.selection-container {
  display: flex;
  flex-direction: row; 
  justify-content: space-between;
  margin: 0px 15px;
  margin-bottom: 15px;
}

.selection0 {
  width: 24%;
  display: flex;
  flex-direction: column;
  text-align: center;
  opacity: 1;
}

.selection1 {
  width: 24%;
  display: flex;
  flex-direction: column;
  text-align: center;
  opacity: .4;
}

.selection2 {
  width: 24%;
  display: flex;
