  white-space: nowrap;

  font-family: Futura;
  font-style: normal;
  font-weight: bold;
  font-size: 9.8px;
  letter-spacing: 0.08em;
  text-transform: uppercase;
  color: #FFFFFF;
}
.heading1 {
  font-family: Futura;
  font-style: normal;
  font-weight: bold;
  font-size: 10px;
