.bar {
  border: 2px solid white;
  border-radius: 20px;
  color: #FFFFFF;
  margin-bottom: 5px;
}

.button-container {
  display: flex;
  justify-content: space-between;
  margin-top: 10px;
}

.add-button {
  background: #F6F6F6;
  border: none;
  border-radius: 5px;

  font-family: Futura;
  font-style: normal;
  font-weight: bold;
  font-size: 10px;
  letter-spacing: 0.08em;
  text-transform: uppercase;
  color: #009CDB;
  padding: 10px 20px;
}

.done-button {
  background: #F6F6F6;
  border: none;
  border-radius: 5px;

  font-family: Futura;
  font-style: normal;
  font-weight: bold;
  font-size: 10px;
  letter-spacing: 0.08em;
  text-transform: uppercase;
  color: #000000;
  padding: 10px 20px;
}

