#app-container {
  display: flex;
  justify-content: center;
}

#button-container {
  position: absolute;
  top: 0;

  width: 375px;
}

#add-button {
  margin-top: 20px;
  margin-left: 10px;

  background: #F6F6F6;
  border: none;
  border-radius: 5px;

  font-family: Futura;
  font-style: normal;
  font-weight: bold;
  font-size: 10px;
  letter-spacing: 0.08em;
  text-transform: uppercase;
  color: #009CDB;
  padding: 10px 20px;
}

