.co-page-container {
  display: flex;
  justify-content: center;
}

.co-page {
  width: 375px;
  /* height: 812px; */

  position: absolute;
  display: flex;
  justify-content: center;
  flex-direction: column;

  background: #FFFFFF;
  padding: 24px 20px;
  padding-bottom: 75px;

  font-family: Futura;
  font-style: normal;
  font-weight: bold;
  font-size: 12px;
}

.co-nav-buttons {
  display: flex;
  justify-content: space-between;

  line-height: 120%;
  letter-spacing: 0.08em;

  color: #009CDB;
}

.co-back {
  display: flex;
  align-items: center;
  justify-content: center;

  width: 97px;
  height: 48px;

  background: #FFFFFF;
  border: 1px solid #E4E4E4;
  box-sizing: border-box;
  border-radius: 10px;
}

.co-back-title {
  font-family: Futura;
  font-style: normal;
  font-weight: bold;
  font-size: 12px;
  line-height: 120%;

  letter-spacing: 0.08em;
  text-transform: uppercase;
  color: #009CDB;
}

.co-keep-shopping {
  display: flex;
  align-items: center;
  justify-content: center;

  width: 158px;
  height: 48px;

  background: #FFFFFF;
  border: 1px solid #E4E4E4;
  box-sizing: border-box;
  border-radius: 10px;
}

.co-your-cart {
  font-size: 20px;
  line-height: 120%;
  letter-spacing: 0.06em;

  color: #222222;
  padding-top: 24px;
}

.co-cartview-buttons {
  margin-top: 16px;

  width: 225px;
  display: flex;

  background: #F6F6F6;
  border: 1px solid #E4E4E4;
  box-sizing: border-box;
  border-radius: 5px;

  font-size: 10px;
  line-height: 120%;

  letter-spacing: 0.08em;

  color: #009CDB;
}

.co-by-scene {
  background: #FFFFFF;
  border-right: 1px solid #E4E4E4;
  box-sizing: border-box;
  border-radius: 5px;

  padding: 16px 20px;
}

.co-itemized {
  padding: 16px 20px;
}

.co-bouquet-summary {
  height: 180px;
  margin-top: 20px;
  padding: 18px;

  display: flex;
  flex-direction: column;
  justify-content: space-between;

  background: linear-gradient(180deg, rgba(255, 255, 255, 0.2) 0%, rgba(255, 255, 255, 0) 100%), #BB4B82;
  border-radius: 10px;

  color: #FFFFFF;
  line-height: 120%;

}

.co-bouquet-title {
  font-size: 18px;
  letter-spacing: 0.08em;
}

.co-bouquet-title-line {
  padding-bottom: 10px;
}

.co-bouquet-subtotal {
  display:flex;
  flex-direction: column;
}

.co-bouquet-subtotal-title {
  letter-spacing: 0.04em;
}

.co-bouquet-subtotal-price {
  align-self: flex-start;
  border: 1px solid #C9719D;
  border-radius: 5px;

  padding: 10px;
  margin-top: 10px;
}


/* --- */
.co-inflation-service {
  height: 92px;

  display: flex;
  flex-direction: column;

  padding: 16px;
  margin-top: 12px;

  background: #FFFFFF;
  border: 1.26207px solid #E4E4E4;
  box-sizing: border-box;
  border-radius: 6.31035px;
  margin-bottom: 10px;
}

.co-inflation-heading {
  display: flex;
  justify-content: space-between;
}

.co-inflation-title {
  display: flex;
  align-items: center;

  font-style: normal;
  font-weight: 500;
  line-height: 120%;
  letter-spacing: 0.04em;
  color: #999999;
}

.co-inflation-price {
  font-size: 14px;
  line-height: 120%;
  text-align: right;
  color: #222222;
}

.co-inflation-option {
  margin-top: 10px;

  display: flex;
  align-items: center;
}

.co-inflation-dropdown {
  /* width: 80.5px;
  height: 36px; */
  
  /* position:absolute;
  left: 73px; */

  display: flex;
  align-items: center;

  background: #FFFFFF;
  border: 1px solid #E3E3E3;
  box-sizing: border-box;
  border-radius: 4px;

  padding: 5px 8px;

  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 120%;
  color: #222222;
}

.darryl {
  height: 45px;
  width: 80.5px;

  position: relative;
  left: 36px;
  top: -5px;

  background: #FFFFFF;
  border: 1px solid #E3E3E3;
  border-top: none;
  box-sizing: border-box;
  border-radius: 4px;
  border-top-left-radius: 0px;
  border-top-right-radius: 0px;

  padding: 5px;
  padding-top: 20px;

  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 120%;
  color: #222222;
}

.co-dropdown-icon {
  display: flex;
  align-self: center;
  justify-content: center;
  
  width: 24px;
  height: 24px;

  background: #009CDB;
  border-radius: 42.8571px;
}
.co-inflation-text {
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 120%;
  color: #222222;
}

.co-inflation-text-ending {
  position: absolute;
  left: 154px;

  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 120%;
  color: #222222;
}

.co-detail-section {
  display: flex;
  align-items: center;

  font-family: Futura;
  font-style: normal;
  font-weight: bold;
  font-size: 12px;
  line-height: 120%;

  text-align: center;
  letter-spacing: 0.08em;
  text-transform: uppercase;
  color: #009CDB;

  margin-top: 15px;
  margin-bottom: 10px;
}



/* --- */

.co-checkout-button {
  display: flex;
  justify-content: center;
  align-items: center;

  background: linear-gradient(180deg, rgba(255, 255, 255, 0.3) 0%, rgba(255, 255, 255, 0) 100%), #009CDB;
  border-radius: 10px;

  font-size: 14px;
  text-align: center;
  letter-spacing: 0.08em;
  line-height: 120%;

  color: #FFFFFF;

  position: fixed;
  width: 335px;
  bottom: 15px;
}


.co-checkout-button-price {
  border: 1px solid #60C1E9;
  border-radius: 5px;

  padding: 7px;
  margin: 12px;
}

.co-checkout-button-title {
}